.cards__bin {
  background-color: initial;
  border: 0;
  width: 18px;
  height: 19px;
  position: absolute;
  padding: 0;
  top: 18px;
  right: 15px;
  cursor: pointer;
  background-image: url("../../../images/bin.svg");
  background-repeat: no-repeat;
  background-position: center;
  transition: opacity ease-in-out 0.4s;
}
.cards__bin:hover {
  opacity: 0.6;
}
.cards__bin_hide {
  display: none;
}
