.popup__input-text {
  border: none;
  border-bottom: 1px solid #ccc;
  color: #000000;
  margin: 0 0 30px;
  padding: 0px 0 13px;
  width: 95%;
  outline: none;
  font-size: 14px;
  line-height: 1.2;
  box-sizing: border-box;
}

.popup__input-text:last-of-type {
  margin-bottom: 0;
}
