.popup-remove__button {
  color: #ffffff;
  font-size: 18px;
  line-height: 1.2;
  width: 95%;
  height: 50px;
  text-align: center;
  background: #000000;
  border-radius: 2px;
  border: none;
  margin: 38px 0 0;
  box-sizing: border-box;
  cursor: pointer;
  transition: opacity ease-in-out 0.4s;
}

.popup-remove__button:hover {
  opacity: 0.8;
}
@media all and (max-width: 682px) {
  .popup-remove__button {
    font-size: 14px;
  }
}
