.profile__name {
  font-size: 42px;
  font-weight: 500;
  line-height: 1.14;
  color: #ffffff;
  margin: 0 0 16px;
  max-width: 294px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

@media all and (max-width: 742px) {
  .profile__name {
    font-size: 27px;
    margin-top: 22px;
    margin-bottom: 14px;
    line-height: 1.22;
  }
}
