.popup-avatar__title {
  color: #000000;
  font-size: 24px;
  line-height: 1.2;
  padding: 34px 0 54px;
  font-weight: 900;
  text-align: left;
  max-width: 100%;
  margin: 0;
  align-self: flex-start;
}

@media all and (max-width: 682px) {
  .popup-avatar__title {
    font-size: 18px;
    padding: 25px 0 75px;
    width: 98%;
  }
}
